<template>
  <div class="footer" id="link">
    <div class="footer-wrap clearfix">
      <div class="footer-wrap-left">
        <p class="footer-wrap-left-title">联系我们</p>
        <div class="footer-wrap-left-links clearfix">
          <p class="weixin_mobile" @click="handleWeiXinClick"></p>
          <p class="weixin"><span class="weixin_code"></span></p>
          <p @click="handleWeiBoClick" class="weibo"></p>
          <p @click="handleTwitterClick" class="twitter"></p>
        </div>
      </div>
      <div class="footer-wrap-right"></div>
    </div>
    <div class="weixin_pop" v-if="showPop" @click="showPop=false">
      <img src="../../assets/img/weixin_code.jpg">
    </div>
  </div>
</template>
<style lang="less" scoped>
  .footer {
    width: 100%;
    height: 425px;
    background: url("../../assets/img/footer.png") no-repeat center;
    background-size: 100% 100%;
    &-wrap {
      width: 1280px;
      margin: 0 auto;
      padding-top: 102px;
      box-sizing: border-box;
      &-left {
        float: left;
        &-title {
          color: #333333;
          font-size: 24px;
          height: 33px;
          line-height: 33px;
        }
        &-links {
          margin-top: 55px;
          p {
            float: left;
            width: 44px;
            height: 44px;
            background: url("../../assets/img/footer_link1.png") no-repeat center;
            background-size: 100% 100%;
            cursor: pointer;
          }
          p.weixin_mobile {
            display: none;
          }
          p.weixin {
            position: relative;
            &:hover {
              .weixin_code {
                display: block;
              }
            }
            .weixin_code {
              display: none;
              position: absolute;
              left: -30px;
              top: 50px;
              width: 100px;
              height: 100px;
              background: url("../../assets/img/weixin_code.jpg") no-repeat center;
              background-size: 100% 100%;
              /*display: none;*/
            }
          }
          .weibo {
            margin: 0 74px;
            background-image: url("../../assets/img/footer_link2.png");
          }
          .twitter {
            background-image: url("../../assets/img/footer_link3.png");
            background-size: 34px 34px;
          }
        }
      }
      &-right {
        float: right;
        margin: 18px 80px 0 0;
        width: 393px;
        height: 135px;
        background: url("../../assets/img/logo.png") no-repeat center;
        background-size: 393px 135px;
      }
    }
    .weixin_pop {
      display: none;
    }
  }
  @media only screen and (max-width: 768px) {
    .footer {
      width: 100%;
      height: 2.7rem;
      background: url("../../assets/img/footer_mobile.png") no-repeat center;
      background-size: 100% 100%;
      &-wrap {
        width: 50%;
        margin: 0 auto;
        overflow: hidden;
        padding: 0.32rem 0 0.36rem 0;
        box-sizing: border-box;
        &-left {
          float: none;
          &-title {
            font-size: 20px;
            line-height: 0.28rem;
            color: #333333;
            text-align: center;
          }
          &-links {
            margin-top: 0.4rem;
            p {
              float: left;
              width: 0.29rem;
              height: 0.29rem;
              background: url("../../assets/img/footer_link1.png") no-repeat center;
              background-size: 100% 100%;
            }
            p.weixin {
              display: none;
            }
            p.weixin_mobile {
              display: block;
            }
           .weibo {
             margin: 0 0.49rem;
              background-image: url("../../assets/img/footer_link2.png");
            }
            .twitter {
              background-image: url("../../assets/img/footer_link3.png");
            }
          }
        }
        &-right {
          float: none;
          width: 1.57rem;
          height: 0.54rem;
          margin: 0.51rem auto 0;
          background: url("../../assets/img/footer_mobile_logo.png") no-repeat center;
          background-size: 100% 100%;
        }
      }
      .weixin_pop {
        display: block;
        position: fixed;
        width: 100%;
        height: 100%;
        left: 0;
        top: 0;
        z-index: 1000;
        background-color: rgba(0,0,0, 0.6);
        img {
          position: absolute;
          width: 3rem;
          height: 3rem;
          left: 50%;
          top: 50%;
          transform: translate(-50%, -50%);
        }
      }
    }
  }
</style>
<script>
  export default {
    data() {
      return {
        showPop: false
      }
    },
    methods: {
      handleWeiXinClick() {
        this.showPop = !this.showPop
      },
      handleWeiBoClick() {
        window.open('https://twitter.com/NOVA19460216')
      },
      handleTwitterClick() {
        window.open('https://t.me/joinchat/HmPclg-HFkOmB9bcLfL_yg')
      }
    }
  }
</script>



