            margin-top: 0.38rem;
            font-size: 24px;
            color: #FFFFFF;
            letter-spacing: 3.24px;
            line-height: 0.38rem;
            text-shadow: 0 0 10px #375B84;
            text-align: center;
          }
          &-btn {
            width: 1.2rem;
            height: 0.44rem;
            margin: 0.7rem auto 0;
            font-size: 0.14rem;
            padding: 0 0.28rem 0 0.25rem;
            box-sizing: border-box;
            line-height: 0.44rem;
            color: #ffffff;
            background-image: linear-gradient(-131deg, #FF8921 0%, #F76B1C 100%);
            border-radius: 0.03rem;
            i {
              margin-right: 0.04rem;
              font-size: 0.16rem;
              vertical-align: middle;
            }
          }
        }
      }
      &-title {
        width: 1.0rem;
        margin: 0 auto;
        .name {
          color: #333333;
          font-size: 20px;
          line-height: 1;
          text-align: left;
        }
        .line {
          width: 0.95rem;
          height: 0.05rem;
          margin-top: 0.06rem;
          background: linear-gradient(to right, #FF9207, rgba(250,217,97, 0));
        }
      }
      &-intro {
        padding-top: 0.5rem;
        background-color: #ffffff;
        box-sizing: border-box;
        .container {
          width: 100%;
          background: none;
          background-size: 0;
        }
        &-wrap {
          width: 100%;
          padding: 0 0.38rem 0.45rem;
          box-sizing: border-box;
        }
        &-text {
          margin-top: 0.4rem;
          P {
            word-break: break-all;
            font-size: 13px;
            opacity: 0.6;
            color: #000000;
            line-height: 0.28rem;
          }
          P:last-child {
            margin-top: 0.6rem;
          }
        }
      }
      &-tech {
        width: 100%;
        height: auto;
        background-color: #ffffff;
        .container {
          width: 100%;
          padding-top: 0.4rem;
          box-sizing: border-box;
          background: url("../../assets/img/home_tech_m.png") no-repeat left top;
          background-size: 100% 3.9rem;
        }
        &-list {
          margin-top: 0.22rem;
          padding: 0;
          &-item {
            float: none;
            width: 100%;
            margin-top: 0.5rem;
            & + .home-tech-list-item {
              margin-left: 0;
            }
            .item-img {
              width: 1.52rem;
              height: 1.52rem;
              text-align: center;
              margin: 0 auto;
              img {
                width: 1.52rem;
                height: 1.52rem;
              }
            }
            .item-title {
              height: auto;
              line-height: 0.3rem;
              margin-top: 0.2rem;
              font-size: 22px;
              color: #333333;
              text-align: center;
            }
            .item-subtitle {
              padding: 0 0.38rem;
              box-sizing: border-box;
              margin-top: 0.1rem;
              line-height: 0.22rem;
              color: #000000;
              opacity: 0.6;
              font-size: 14px;
              text-align: center;
            }
          }
        }
      }
      &-team {
        .container {
          width: 100%;
        }
        &-wrap {
          padding: 0;
          box-sizing: border-box;
          &-line {
           display: none;
          }
          .home-title {
            margin-top: 1rem;
          }
          &-list {
            margin-top: 0;
            padding: 0 0.38rem 0.5rem;
            box-sizing: border-box;
            .team-item {
              float: none;
              width: 100%;
              margin-top: 0.5rem;
              & + .team-item {
                margin-left: 0;
              }
              &-img {
                width: 0.74rem;
                height: 0.74rem;
                margin: 0 auto;
                background: url("../../assets/img/team_item1.png") no-repeat center;
                background-size: 100% 100%;
              }
              &:nth-child(2) > .team-item-img {
                background-image: url("../../assets/img/team_item2.png");
              }
              &:nth-child(3) > .team-item-img {
                background-image: url("../../assets/img/team_item3.png");
              }
              &-name {
                text-align: center;
                opacity: 0.8;
                font-size: 18px;
                color: #000000;
                margin-top: 0.3rem;
                line-height: 0.25rem;
              }
              &-text {
                text-align: center;
                margin-top: 0.16rem;
                opacity: 0.6;
                font-size: 12px;
                color: #000000;
                line-height: 0.2rem;
              }
            }
          }
        }
      }
      &-invest {
        width: 100%;
        background-color: #EEEEEE;
        .container {
          width: 100%;
          padding-top: 0.3rem;
          box-sizing: border-box;
          background: url("../../assets/img/home_invest_mobile.png") no-repeat center;
          background-size: 100% 100%;
        }
        &-swiper {
          display: none;
        }
        &-mobile-swiper {
          display: block;
          width: 80%;
          height: 3.3rem;
          margin-top: 0.4rem;
          .slide-wrap {
            width: 100%;
            margin: 0 auto;
            .slide-wrap-item {
              width: 100%;
              height: 3.3rem;
              padding: 0.25rem 0.36rem 0.15rem 0.36rem;
              box-sizing: border-box;
              border-radius: 0.04rem;
              background-color: #ffffff;
              &-img {
                width: 0.6rem;
                height: 0.6rem;
                margin: 0 auto;
                img {
                  width: 100%;
                  height: 100%;
                }
              }
              &-name {
                text-align: center;
                font-size: 18px;
                color: #000000;
                opacity: 0.8;
                line-height: 0.25rem;
                margin-top: 0.2rem;
              }
              &-info {
                margin-top: 0.24rem;
                opacity: 0.6;
                font-size: 12px;
                color: #000000;
                line-height: 0.2rem;
              }
            }
          }
        }
        &-join {
          width: 100%;
          width: 100%;
          margin: 0;
          &-img {
            float: none;
            display: block;
            &:nth-child(1) {
              width: 1.22rem;
              height: 0.3rem;
              margin: 0.5rem auto 0.32rem;
              background: url("../../assets/img/invest-join1.png") no-repeat center;
              background-size: 100%;
            }
            &:nth-child(2) {
              width: 1.04rem;
              height: 0.54rem;
              margin: 0.32rem auto 0;
              background: url("../../assets/img/invest-join2.png") no-repeat center;
              background-size: 100%;
            }
            &:nth-child(3) {
              width: 1.45rem;
              height: 0.47rem;
              margin: 0.32rem auto 0;
              background: url("../../assets/img/invest-join3.png") no-repeat center;
              background-size: 100%;
            }
            &:nth-child(4) {
              width: 1.51rem;
              height: 0.33rem;
              margin: 0.32rem auto 0;
              background: url("../../assets/img/invest-join4_m.png") no-repeat center;
              background-size: 100%;
            }
            &:nth-child(5) {
              width: 1.37rem;
              height: 0.30rem;
              margin: 0.32rem auto 0;
              background: url("../../assets/img/invest-join5_m.png") no-repeat center;
              background-size: 100%;
            }
            &:nth-child(6) {
              width: 1.13rem;
              height: 0.40rem;
              margin: 0.32rem auto 0.79rem;
              background: url("../../assets/img/invest-join6_m.png") no-repeat center;
              background-size: 100%;
            }
          }
        }
      }
      &-media {
        width: 100%;
        background-color: #ffffff;
        &-wrap {
          width: 100%;
          margin: 0;
          padding: 0.93rem 0.38rem 1.25rem 0.38rem;
          box-sizing: border-box;
          &-list {
            display: none;
          }
          &-swiper {
            display: block;
            margin-top: 0.35rem;
            .slide-wrap-img {
              width: 2.7rem;
              height: 1.5rem;
              margin: 0 auto;
              img {
                width: 100%;
                height: 1.5rem;
              }
            }
            .slide-wrap-title {
              opacity: 0.8;
              font-size: 16px;
              color: #000000;
              line-height: 0.22rem;
              margin-top: 0.4rem;
              white-space: nowrap;
              letter-spacing: 0;
            }
            .slide-wrap-time {
              opacity: 0.4;
              font-size: 14px;
              color: #000000;
              letter-spacing: 0;
              line-height: 0.2rem;
              margin-top: 0.12rem;
              text-align: center;
            }
            .slide-wrap-detail {
              opacity: 0.6;
              font-size: 14px;
              color: #000000;
              line-height: 0.26rem;
              margin-top: 0.3rem;
            }
            .slide-wrap-more {
              display: block;
              font-size: 14px;
              margin-top: 0.15rem;
              line-height: 0.2rem;
              text-align: center;
              color: #FF8903;
            }
          }
        }
      }
    }
  }
</style>
<script>
  import vHeader from '@/components/header/index'
  import vFooter from '@/components/footer/index'
  import $ from 'jquery'
  export default {
    data() {
      return {
        swiperOption: {
          direction: 'vertical',
          autoplay: {
            disableOnInteraction: false,
            delay: 3000
          },
          pagination: {
            el: '.swiper-pagination',
            clickable: true
          }
        },
        mobielSwiperOption: {
          autoplay: {
            disableOnInteraction: false,
            delay: 3000
          }
        }
      }
    },
    components: {
      vHeader,
      vFooter
    },
    methods: {
      //  滚动事件
      goSscroll(selector, height) {
        $('html, body').animate({
          scrollTop: $(selector).offset().top - height
        }, 1000)
      },
      handleSeePdfClick() {
        window.open('/#/pdf')
      },
      handleOpenClick() {
        window.open('http://new.xinhuanet.com.lv/a/2018/0506/3167.html')
      }
    }
  }
</script>



