<template>
  <div class="header" :class="{collapse: menuOpen===true}">
    <div class="container clearfix">
      <div class="header-left pull-left">
        <p class="header-left-logo"></p>
        <ul class="header-left-menu clearfix">
          <li :class="{'is-active': active === '#home'}" @click="handleScrollClick('#home', 64)">首页</li>
          <li :class="{'is-active': active === '#intro'}" @click="handleScrollClick('#intro', 64)">简介</li>
          <li :class="{'is-active': active === '#tech'}" @click="handleScrollClick('#tech', 64)">技术介绍</li>
          <li :class="{'is-active': active === '#team'}" @click="handleScrollClick('#team', 64)">团队介绍</li>
          <li :class="{'is-active': active === '#invest'}" @click="handleScrollClick('#invest', 64)">投资顾问</li>
          <li :class="{'is-active': active === '#media'}" @click="handleScrollClick('#media', 64)">媒体报道</li>
          <li :class="{'is-active': active === '#link'}" @click="handleScrollClick('#link', 64)">联系我们</li>
        </ul>
      </div>
      <div class="header-right pull-right">
        <p class="header-right-lan">中文<i class="iconfont icon-pulldown"></i></p>
        <a class="header-right-down" href="/static/nova.pdf" download="nova"><i class="iconfont icon-down"></i>白皮书</a>
      </div>
    </div>
    <div class="header-mobile">
      <div class="header-mobile-nav clearfix">
        <p class="header-mobile-nav-logo pull-left"></p>
        <p class="header-mobile-nav-menu iconfont icon-menu pull-right" @click="handleMenuClick"></p>
      </div>
      <div class="header-mobile-menu">
        <ul>
          <li @click="handleScrollClick('#intro', 44)">简介</li>
          <li @click="handleScrollClick('#tech', 44)">技术介绍</li>
          <li @click="handleScrollClick('#team', 44)">团队介绍</li>
          <li @click="handleScrollClick('#invest', 44)">投资顾问</li>
          <li @click="handleScrollClick('#media', 44)">媒体报道</li>
          <li @click="handleScrollClick('#link', 44)">联系我们</li>
        </ul>
      </div>
    </div>
  </div>
</template>
<style lang="less" scoped>
  .header {
    position: fixed;
    left: 0;
    top: 0;
    z-index: 1000;
    width: 100%;
    height: 64px;
    font-size: 14px;
    background-color: #ffffff;
    &-left {
      margin-left: 44px;
      height: 100%;
      &-logo {
        width: 103px;
        height: 64px;
        display: inline-block;
        background: url("../../assets/img/header_logo.png") no-repeat left 15px;
        background-size: 100% 34px;
      }
      &-menu {
        display: inline-block;
        margin-left: 70px;
        height: 64px;
        color: #999999;
        li {
          float: left;
          line-height: 64px;
          cursor: pointer;
          & + li {
            margin-left: 40px;
          }
          &.is-active {
            color: #F5A623;
          }
          &:hover {
            color: #F5A623;
          }
        }
      }
    }
    .header-right {
      margin-right: 20px;
      &-lan {
        line-height: 64px;
        display: inline-block;
        color: #9C9C9C;
        cursor: pointer;
        i {
          margin-left: 7px;
          font-size: 6px;
          vertical-align: middle;
        }
      }
      &-down {
        display: inline-block;
        padding: 0 11px;
        box-sizing: border-box;
        height: 36px;
        line-height: 36px;
        margin: 14px 40px 0;
        border-radius: 2px;
        font-size: 14px;
        color: #ffffff;
        background-color: #F5A623;
        cursor: pointer;
        i {
          font-size: 17px;
          margin-right: 3px;
          vertical-align: middle;
        }
        &:hover {
          opacity: 0.9;
        }
      }
    }
    &-mobile {
      display: none;
    }
  }
  //  移动端适配
  @media only screen and (max-width: 768px) {
    .header {
      width: 100%;
      height: auto;
      .container {
        display: none;
      }
      &-mobile {
        display: block;
        &-nav {
          height: 44px;
          background-color: #ffffff;
          &-logo {
            margin-left: 14px;
            width: 62px;
            height: 44px;
            background: url("../../assets/img/header_logo.png") no-repeat left 12px;
            background-size: 100% 20px;
          }
          &-menu {
            margin-right: 12px;
            font-size: 20px;
            margin-top: 12px;
            color: #CCCCCC;
          }
        }
        &-menu {
          display: none;
          padding-top: 0.08rem;
          box-sizing: border-box;
          ul {
            display: block;
            li {
              height: 0.36rem;
              line-height: 0.36rem;
              font-size: 13px;
              color: #666666;
              padding: 0 0.2rem;
              border-bottom: 1px solid #E9E9E9;
              box-sizing: border-box;
            }
          }
        }
      }
    }
    .collapse {
      height: 100%;
      background-color: #F5F5F5;
      .header-mobile-menu {
        display: block !important;
      }
    }
  }
</style>
<script>
  import $ from 'jquery'
  export default {
    data() {
      return {
        active: '#home',
        menuOpen: false
      }
    },
    methods: {
      //  点击菜单
      handleScrollClick(selector, height) {
        this.active = selector
        this.menuOpen = false
        this.$emit('headerClick', selector, height)
      },
      //  移动端开关菜单栏
      handleMenuClick() {
        this.menuOpen = !this.menuOpen
      }
    }
  }
</script>



